label, .html-wrap .nf-field-element {
  font-family: "tiempos", serif;
  font-weight: 700;
  font-size: 18px;
  color: #1d1d1d; }

.nf-field-element p, .nf-field-element ul {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 1.125rem;
  line-height: 1.675;
  font-weight: 400; }

.form-control {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  color: #1d1d1d; }

.custom-control-label {
  line-height: 1.8; }

.fieldset--border {
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  padding: 15px; }

.label-big .custom-control {
  padding-left: 0; }

.label-big__label {
  width: 100%;
  padding: 0px 15px 0px 2rem;
  font-size: 1.125rem;
  line-height: 1.4;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400; }
  .label-big__label:before, .label-big__label:after {
    left: 0 !important; }

.label-big__label:before {
  background: white;
  border: 1px solid #e4e4e4; }

.label-big__description {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 21px;
  color: #444444; }

.label-big__info {
  font-size: 19px; }
  @media (min-width: 1024px) {
    .label-big__info {
      float: right; } }

.form-messages {
  display: none; }

.form-success,
.form-error {
  display: none; }

input[type=file] {
  -webkit-appearance: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  padding: 0; }

input[type=file] + label {
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  cursor: pointer;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  margin: 0 20px 0 0; }

input[type=file] + label + span {
  font-size: 18px;
  height: 48px;
  line-height: 48px;
  justify-content: center;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

input[type=file] + label * {
  pointer-events: none; }

.custom-control {
  padding-left: 0; }

.custom-checkbox:hover,
.custom-radio:hover {
  cursor: pointer; }
  .custom-checkbox:hover .label-big__label:before,
  .custom-radio:hover .label-big__label:before {
    background: #d1d1d1; }

.page--form input:required {
  position: relative;
  width: calc(100% - 30px);
  display: inline-block; }

.page--form input:required + .requiredInput {
  display: inline-block;
  color: #1d1d1d;
  margin-left: 1rem; }

.page--form form input,
.page--form form select,
.page--form form textarea {
  margin-bottom: 1rem; }

.page--form form label {
  position: relative;
  top: -.5rem;
  margin-bottom: 0rem; }

.page--form form.submit-attempted input:invalid {
  border: 1px solid red; }

.page--whitepaper form {
  border-top: 1px solid #d1d1d1;
  margin-top: 3rem;
  padding-top: 3rem; }

/* ninjaforms */
input[type=search], input[type=submit], input[type=text], input[type=textarea] {
  -webkit-appearance: none; }

input {
  -webkit-appearance: none; }

input[type=text],
input[type=email],
textarea,
select {
  font-family: "Roboto", Arial, sans-serif;
