::-moz-placeholder {
  /* Firefox 19+ */
  color: #AEAEAE; }

:-ms-input-placeholder {
  color: #AEAEAE; }

[type="radio"] + label:before {
  content: '';
  display: inline-block;
  width: 20px;
  background: #FFF;
  height: 20px;
  margin: 0 10px 0 0;
  border-radius: 50%;
  border: 1px solid #E6E2CE;
  transition: 0.5s ease all;
  background-position: center;
  vertical-align: sub; }

[type="radio"] {
  display: none; }

[type="radio"] + label {
  text-indent: 0 !important; }

[type="radio"]:checked + label:before {
  border-color: #F5AFA4;
